import photo from './images/photo.jpg';
import git from './images/github.svg';
import linkedin from './images/linkedin.svg';
import telegram from './images/telegram.svg';
import React from "react";

const Contactme = () => {
  return (
    <section id='contact'>
      <div className='container'>
        <p className='title'>Contact me</p>
        <div className='row'>
          <div className='column lang-center col-1'>
            <img className='contact__photo' src={photo} alt="me"/>
          </div>
          <div className='column contact__col col-1'>
            <div className='contact__links'>
              <a className='contact__link' href="https://github.com/SleLLl" target='_blank'>
                <img className='contact__photo' src={git} alt="git"/>
              </a>
              <a className='contact__link' href="https://www.linkedin.com/in/slell1/" target='_blank'>
                <img className='contact__photo' src={linkedin} alt="linkedin"/>
              </a>
              <a className='contact__link' href="https://t.me/slell1" target='_blank'>
                <img className='contact__photo' src={telegram} alt="telegram"/>
              </a>
            </div>
            <a className='contact__title' href="mailto:slelllq@gmail.com">slelllq@gmail.com</a>
            <a className='contact__subtitle' href="tel:+375297055797">+375 29 705 57 97</a>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Contactme;
