import React from 'react';
import {VerticalTimeline, VerticalTimelineElement} from "react-vertical-timeline-component";

const Experiance = () => {
  return (
    <section id='experience'>
      <div className='container'>
        <p className='title'>Work experience</p>
        <VerticalTimeline>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{background: 'rgb(234,199,198)', color: '#000000'}}
            date="Sep 2020 - present"
            iconStyle={{background: 'rgb(181,118,180)', color: '#fff'}}

          >
            <h3 className="vertical-timeline-element-title">Freelance</h3>
            <h4 className="vertical-timeline-element-subtitle">Front-end developer</h4>
            <p>
              Created microsites, landing pages for Upwork using CMS, XHTML, HTML5,CSS and JS.
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="Sep 2019 - Jun 2020"
            contentStyle={{background: 'rgb(255,216,181)', color: '#0e0b0b'}}
            iconStyle={{background: 'rgb(205,110,27)', color: '#fff'}}
          >
            <h3 className="vertical-timeline-element-title">DVCh-Management LLC</h3>
            <h4 className="vertical-timeline-element-subtitle">Energy Engineer</h4>
            <p>
              Technical operation of devices and apparatus
            </p>
          </VerticalTimelineElement>
        </VerticalTimeline>
      </div>
    </section>
  )
}

export default Experiance;
