import React from 'react';
import {VerticalTimeline, VerticalTimelineElement} from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

const Education = () => {
  return (
    <section className='education' id='education'>
      <div className='container'>
        <p className='title'>Education</p>
        <VerticalTimeline>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            contentStyle={{background: 'rgb(243,191,33)', color: '#fff'}}
            contentArrowStyle={{borderRight: '7px solid  rgb(33, 150, 243)'}}
            date="Sep 2020 - present"
            iconStyle={{background: 'rgb(33, 150, 243)', color: '#fff'}}

          >
            <h3 className="vertical-timeline-element-title">IT School Intellect Fox</h3>
            <p>
              Front-end developer course
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="May 2020 - Juli 2020"
            contentStyle={{background: 'rgb(148,69,248)', color: '#fff'}}
            iconStyle={{background: 'rgb(243,33,40)', color: '#fff'}}
          >
            <h3 className="vertical-timeline-element-title">Udemi</h3>
            <p>
              JavaScript + React course
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="Sep 2019 - Jun 2020"
            iconStyle={{background: 'rgb(21,105,91)', color: '#fff'}}
          >
            <h3 className="vertical-timeline-element-title">JavaRush</h3>
            <p>
              Java 32lvl
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="2018 - present"
            iconStyle={{background: 'rgb(90,6,54)', color: '#fff'}}

          >
            <h3 className="vertical-timeline-element-title">BNTU</h3>
            <h4 className="vertical-timeline-element-subtitle">Mins</h4>
            <p>
              Electrician Engineer
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--education"
            date="2012 - 2016"
            contentStyle={{background: 'rgb(26,202,42)', color: '#fff'}}
            iconStyle={{background: 'rgb(233, 30, 99)', color: '#fff'}}

          >
            <h3 className="vertical-timeline-element-title">Branch of BNTU</h3>
            <p>
              Electromechanical Technician
            </p>
          </VerticalTimelineElement>
        </VerticalTimeline>
      </div>
    </section>
  )
}

export default Education;
