import React from 'react';
import rus from './images/russia.svg';
import eng from './images/unt.svg';

const Languages = () => {
  return (
    <section id='languages'>
      <div className='container'>
        <p className='title'>Languages</p>
        <div className='row'>
          <div className='column lang-center col-1'>
            <img className='lang__photo' src={rus} alt="rus"/>
            <p className='lang__title'>Russian</p>
            <p className='lang__subtitle'>Native – C2</p>
          </div>
          <div className='column lang-center col-1'>
            <img className='lang__photo' src={eng} alt="rus"/>
            <p className='lang__title'>English</p>
            <p className='lang__subtitle'>Elementary – A2</p>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Languages;
