import React from 'react';
import babel from './images/svgskills/babel.svg';
import css from './images/svgskills/css3.svg';
import figma from './images/svgskills/Frame.svg';
import git from './images/svgskills/git.svg';
import github from './images/svgskills/github.svg';
import html from './images/svgskills/HTML5.svg';
import jsc from './images/svgskills/javascript.svg';
import jss from './images/svgskills/jss.svg';
import ngix from './images/svgskills/nginx.svg';
import node from './images/svgskills/node.svg';
import npm from './images/svgskills/npm.svg';
import pug from './images/svgskills/pug.svg';
import react from './images/svgskills/react.svg';
import rr from './images/svgskills/react-router.svg';
import red from './images/svgskills/redux.svg';
import reds from './images/svgskills/redux-saga.svg';
import sass from './images/svgskills/sass.svg';
import webp from './images/svgskills/webpack.svg';
import vec from './images/Vector.svg';
import Rating from '@material-ui/lab/Rating';
import Box from '@material-ui/core/Box';

const Skills = () => {

  const showCard = (e) => {
    const cards = document.querySelectorAll('.skill__elem:not(:first-of-type)');
    if (e.target.classList.contains('skills__activ')) {
      e.target.classList.remove('skills__activ');
      cards.forEach(item => item.style.display = 'none');
    } else {
      e.target.classList.add('skills__activ');
      cards.forEach(item => item.style.display = 'block');
    }
  }

  return (
    <section id='skills'>
      <div className='container'>
        <p className='title'>Skills</p>
        <div className='row'>
          <div className='column-6 col-3'>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={html} alt="html"/>
              </div>
              <p className='skill__title'>HTML</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={5} readOnly/>
              </Box>
            </div>
            <div className='skill__elem '>
              <div className='skill__photo'>
                <img src={pug} alt="pug"/>
              </div>
              <p className='skill__title'>Pug</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={3} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={rr} alt="react-rout"/>
              </div>
              <p className='skill__title'>React Router</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={3} readOnly/>
              </Box>
            </div>
          </div>
          <div className='column-6 col-3'>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={css} alt="css"/>
              </div>
              <p className='skill__title'>CSS</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={5} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={webp} alt="webpack"/>
              </div>
              <p className='skill__title'>Webpack</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={4} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={red} alt="redux"/>
              </div>
              <p className='skill__title'>Redux</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={3} readOnly/>
              </Box>
            </div>
          </div>
          <div className='column-6 col-3'>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={sass} alt="sass"/>
              </div>
              <p className='skill__title'>SASS</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={4} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={babel} alt="babel"/>
              </div>
              <p className='skill__title'>Babel</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={3} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={reds} alt="redux-saga"/>
              </div>
              <p className='skill__title'>Redux-Saga</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={2} readOnly/>
              </Box>
            </div>
          </div>
          <div className='column-6 col-3'>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={git} alt="git"/>
              </div>
              <p className='skill__title'>Git</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={3} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={node} alt="node"/>
              </div>
              <p className='skill__title'>Node.js</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={1} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={jss} alt="jss"/>
              </div>
              <p className='skill__title'>JSS</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={2} readOnly/>
              </Box>
            </div>
          </div>
          <div className='column-6 col-3'>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={jsc} alt="jsc"/>
              </div>
              <p className='skill__title'>JavaScript</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={4} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={npm} alt="npm"/>
              </div>
              <p className='skill__title'>npm</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={2} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={figma} alt="figma"/>
              </div>
              <p className='skill__title'>Figma</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={3} readOnly/>
              </Box>
            </div>
          </div>
          <div className='column-6 col-3'>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={github} alt="github"/>
              </div>
              <p className='skill__title'>Github</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={4} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={react} alt="react"/>
              </div>
              <p className='skill__title'>React</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={4} readOnly/>
              </Box>
            </div>
            <div className='skill__elem'>
              <div className='skill__photo'>
                <img src={ngix} alt="ngix"/>
              </div>
              <p className='skill__title'>Nginx</p>
              <Box component="fieldset" mb={3} borderColor="transparent">
                <Rating name="read-only" value={1} readOnly/>
              </Box>
            </div>
          </div>
        </div>
        <div className='row skill__v'>
          <img className='skill__vector' onClick={(e) => showCard(e)} src={vec} alt="vector"/>
        </div>
      </div>
    </section>
  )
}

export default Skills;
