import React from 'react';
import photo from './images/photo.jpg';
import Nav from "./Nav";
import cv from './cv/cv.pdf';

const Header = () => {

  const menu = (
    <div className='nav'>
      <a href="#about">About</a>
      <a href="#skills">Skills</a>
      <a href="#experience">Experience</a>
      <a href="#projects">Projects</a>
      <a href="#education">Education</a>
      <a href="#languages">Languages</a>
      <a href="#contact">Contact me</a>
    </div>
  );

  return (
    <header className='header' id='about'>
      <div className='container'>
        <div className='header__nav'>
          {window.innerWidth > 800 ? menu : <Nav/>}
        </div>
        <hr className='header__line'/>
        <div className='row header__reverse'>
          <div className='column col-1 lang-center'>
            <h1 className='header__title'>Sergei Kolyago</h1>
            <h1 className='header__title--dev'>Front-end developer</h1>
            <p className='header__subtitle'>
              Hi, I am Sergei Kolyago and I am Junior Front-end developer.
              <br/>
              <br/>
              I have completed Front-end courses at IT School Intellect Fox and have basic knowledge of HTML, CSS,
              JavaScript, React and Redux.
            </p>

            <a href={cv} download=''>
              <button className='header__btn'>Download CV</button>
            </a>
          </div>
          <div className='column col-1 lang-center'>
            <img className='header__photo' src={photo} alt="me"/>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header;
