import React from 'react';
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';
import trip from './images/TripGouse.jpg';
import food from './images/food.jpg';

const Project = () => {
  return (
    <section id='projects'>
      <div className='container'>
        <p className='title'>Projects</p>
        <AwesomeSlider>
          <div className='project__slide'>
            <div className='project__col'>
              <img className='project__img' src={trip} alt="house"/>
            </div>
            <div className='project__col'>
              <p className='project__title'>Triphouse</p>
              <p className='project__subtitle'>Landing-page for online travel agency for lodging reservations.</p>
              <a className='project__link' href="https://github.com/SleLLl/if-html" target='_blank'>See on Github</a>
            </div>
          </div>
          <div className='project__slide'>
            <div className='project__col'>
              <img className='project__img' src={food} alt="food"/>
            </div>
            <div className='project__col'>
              <p className='project__title'>FoodStore</p>
              <p className='project__subtitle'>Landing-page online food shopping.</p>
              <a className='project__link' href="https://github.com/SleLLl/Food-store" target='_blank'>See on Github</a>
            </div>
          </div>


        </AwesomeSlider>
      </div>
    </section>
  )
}

export default Project;
